  /* ------------- */
  
  .barra{
    width: 30px;
    height: 3px;
    background-color: white;
    margin: 5px 0;
  
  }


  .dropdown-btn{
    
    /* margin-top: 1px; */
    position: relative;
    /* background-color: #000; */
    /* color: white; */
    background-color: rgba(255, 255, 255, 0);;
    
    font-size: 16px;
    
    height: 40px;
    padding-left: 4px;
    padding-right: 4px;
    border: none;
    cursor: pointer;

    transition: 0.3s;

    /* top:  -5px; */
    
    
    
    
}

.dropdown {
    float: right;
    position: relative;
    top: 10%;
    
    z-index:50;
    -webkit-transform: translate3d(0,0,50px);
    transform: translate3d(0,0,50px);   
    right: 100% - 40px; 
}

.dropdown-content{
    display: none;
    position: fixed;
    right: 1px;
    /* float: left; */
    /* left: 50px; */
    /* referente al padre que este tiene */
    background-color: black;
    min-width: 160px;
    max-height: 0;
    overflow: hidden;
    /* border-bottom-right-radius: 25px;  */  
    -webkit-transition: max-height 0.2s ease-in;
    transition: max-height 0.2s ease-in;
    text-align: center;
    

}

.dropdown-content a{
   /*  vertical y el otro horizontal */
    padding: 12px 16px;
    color: white;
    text-decoration: none;
    display: block;
    
}

.dropdown-content a:hover {
    background-color: #f1f1f1;
    color: black;
    
    
    
}

/* cuando estemos sobre la lista. pero solo sobre el boton */
.dropdown:hover .dropdown-btn {
    background-color: #000;
    transform: scale(1.05, 1.05);
}

.dropdown:hover .barra {
    background-color: white;
    
}

/* lo que hace aca que cuando estoy sobre dropdown, ala propiedad de .dropdown-content la cambio */
.dropdown:hover .dropdown-content {
    display: block;
    max-height: 1000px;
}

